body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
li:hover{color: #306EE7;}
.p1{color: pink;}
/* html,body{background-color: rgb(199,237,204)} */
html,body{background-color: rgb(204,232,207)}
.warning{color: #f41;}
span{margin-left: 50px;}
th{width:100px;text-align:center;font-size:18px;line-height:2em;}
td{width:100px;text-align:center;font-size:16px;line-height:2em;}
form{padding:40px 20px;}
label{font-size:18px;}
input{width:100%;height:32px;font-size:16px;}
button{font-size:18px;margin-top:10px;padding:5px 10px;}